@use "../../../../styles/variables" as *;
.nav-buttons {
  grid-column: 4/7;
  display: grid;
  grid-template-columns: repeat(2, 14rem);
  justify-content: center;
  height: 4.2rem;
  margin: auto;
  gap: 2rem;
  justify-items: center;
  padding: 5rem 0 7rem 0;

  .nav-button {
    display: flex;
    width: 100%;
    justify-content: center;
    align-items: center;
    gap: 1.2rem;
    color: $dark-green;
    font-size: 1.6rem;
    background-color: white;
    border-radius: 0.6rem;
    border: 0.1rem solid $dark-green;
    line-height: 0.5rem;
    cursor: pointer;
    transition: 0.3s all;
    box-sizing: border-box;

    &:after,
    &:before {
      //   content: "";
      width: 1.5rem;
      height: 1.5rem;
      background-size: 1.5rem !important;
      background: url("../../../../../public/icons/arrow-down_green.svg")
        no-repeat center;
      transition: 0.3s all;
    }

    &.previous {
      padding: 1rem 1rem 1rem 2rem;
      grid-column: 1/2;

      &:before {
        content: "";
        rotate: 90deg;
      }
    }

    &.next {
      padding: 1rem 2rem 1rem 1rem;
      grid-column: 2/3;

      &:after {
        content: "";
        rotate: -90deg;
      }
    }

    &:hover {
      background-color: $dark-green;
      color: white;

      &:after,
      &:before {
        background-size: 1.5rem !important;
        background: url("../../../../../public/icons/arrow-down_white.svg")
          no-repeat center;
      }
    }
  }
}
